.cookie-bar {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top-width: 1px;
  --border-opacity: 1;
  border-color: #f1f1f1;
  border-color: rgba(241, 241, 241, var(--border-opacity));
  visibility: hidden;
  background: #fff;
  -webkit-transform: translate3d(0, 40px, 0);
  transform: translate3d(0, 40px, 0);
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0s 0.6s,
    -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1),
    opacity 0.3s ease, visibility 0s 0.6s;
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1),
    opacity 0.3s ease, visibility 0s 0.6s,
    -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  top: 72px;
}

.cookie-bar > span {
  font-family: Lausanne, sans-serif;
  font-size: 1.3rem;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  padding-left: 1.2rem;
}

.cookie-bar > span a {
  position: relative;
  transition: color 0.2s ease;
}

.cookie-bar > span a:after,
.cookie-bar > span a:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -0.2rem;
  border-bottom: 1px solid #13110c;
  pointer-events: none;
}

.cookie-bar > span a:before {
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
}

.cookie-bar > span a:after,
.cookie-bar > span a:before {
  transition: -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1),
    -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.cookie-bar > span a:before {
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  transition-delay: 0;
}

.cookie-bar > span a:after {
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  transition-delay: 0.16s;
}

.cookie-bar > span a:hover:after,
.cookie-bar > span a:hover:before {
  transition: -webkit-transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1),
